<!--
 * @Descripttion:
 * @Date: 2020-09-30 17:03:56
-->
<template>
  <div class="div_main">
    <el-row>
      <el-button
        v-if="isLogin"
        style="float: right"
        type="text"
        icon="el-icon-right"
        @click="toAdmin"
        >admin</el-button
      >
      <el-button
        v-else
        size="mini"
        style="float: right"
        type="text"
        @click="dialogVisible = true"
      >
        <i class="el-icon-s-promotion" />
        login</el-button
      >
    </el-row>
    <h1>{{ slogan }}</h1>
    <el-divider>
      <i class="el-icon-ship" />
    </el-divider>
    <el-timeline>
      <el-timeline-item
        v-for="(item, index) in data"
        :key="index"
        color="#67C23A"
        size="large"
        type="succuss"
        :timestamp="item.contentDate"
        placement="top"
      >
        <el-card v-if="checkAuto(item) == null">
          <p class="content">{{ item.content }}</p>
          <img
            v-if="item.pic"
            :src="ossUrl + item.pic"
            fit="cover"
            style="width: 100%; height: 95%"
          />
        </el-card>
        <iframe
          v-else
          frameborder="no"
          border="0"
          marginwidth="0"
          marginheight="0"
          height="86"
          :src="'//music.163.com/outchain/player?type=2&id='+item.autoId+'&auto=0&height=66'"
        ></iframe>
      </el-timeline-item>
      <el-timeline-item
        color="#67C23A"
        size="large"
        type="succuss"
        :timestamp="
          new Date().toLocaleDateString() +
          ' ' +
          new Date().toLocaleTimeString()
        "
        placement="top"
      />
      <p>End</p>
    </el-timeline>
    <footerOh />
    <el-dialog title="login" :visible.sync="dialogVisible" width="90%">
      <el-form
        ref="loginForm"
        :rules="rules"
        :model="loginForm"
        label-width="20px;"
      >
        <el-form-item prop="userName" label="用户名">
          <el-input
            placeholder="输入用户名"
            clearable
            v-model="loginForm.userName"
          />
        </el-form-item>
        <el-form-item prop="password" label="密码">
          <el-input
            type="password"
            placeholder="填写密码"
            clearable
            v-model="loginForm.password"
            :show-password="true"
            @keyup.enter.native="login()"
          />
        </el-form-item>
        <el-form-item>
          <el-button
            style="float: right"
            size="small"
            type="success"
            round
            @click="login()"
          >
            <i class="el-icon-s-promotion" /> login</el-button
          >
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import footerOh from '@/components/footerOh.vue'
import { setToken, getToken, setSlogan, getSlogan } from '@/utils/auth'
export default {
  name: 'mainP',
  components: {
    footerOh,
  },
  data() {
    var validatePasssWd = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('password'))
      } else {
        if (value.length < 6 || value.length > 16) {
          callback(new Error('长度至少6位,不超过16位'))
        }
        callback()
      }
    }
    return {
      dialogVisible: false,
      isLogin: false,
      slogan: '',
      ossUrl: 'https://snkae-1301702299.cos.ap-nanjing.myqcloud.com/',
      //baseUrl: 'http://localhost:5207/api/',
      baseUrl: 'http://47.97.192.85:5207/api/',
      count: 0,
      queryForm: {
        page: 0,
        pageSize: 10,
      },
      loginForm: {
        userName: '',
        password: '',
      },
      rules: {
        userName: [{ required: true, trigger: 'blur', message: 'username' }],
        password: [
          { validator: validatePasssWd, required: true, trigger: 'blur' },
        ],
      },
      data: [],
    }
  },
  created() {
    if (getToken() === '822017161') {
      this.isLogin = true
    }
    this.query()
    this.slogan = getSlogan()
    if (this.slogan === undefined)
      this.axios.get(this.baseUrl + 'slogan').then((res) => {
        console.log(res.data.slogan)
        this.slogan = res.data.slogan
        setSlogan(this.slogan)
      })
  },
  methods: {
    checkAuto(item) {
      var str = item.content
      var start = str.indexOf('song/')
      var end = str.indexOf('/?userid')
      if (end != -1 && start != -1) {
        item.autoId = str.substring(start + 5, end)
        return str.substring(start + 5, end)
      }
      return null
    },
    toAdmin() {
      this.$router.push({ path: this.redirect || '/admin' })
    },
    login() {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          this.axios
            .post(this.baseUrl + 'login', this.loginForm)
            .then((res) => {
              if (res.data.code === 200) {
                this.isLogin = true
                setToken(res.data.token)
                this.dialogVisible = false
                this.$router.push({ path: this.redirect || '/admin' })
              } else {
                this.$message({
                  type: 'error',
                  message: '密码或用户名错误',
                })
              }
            })
        }
      })
    },
    query() {
      this.axios.post(this.baseUrl + 'query', this.queryForm).then((res) => {
        this.count = res.data.count
        res.data.data.forEach((element) => {
          this.data.push(element)
        })
      })
    },
    scroll() {
      let isLoading = false
      window.onscroll = () => {
        // 距离底部200px时加载一次
        let bottomOfWindow =
          document.documentElement.offsetHeight -
            document.documentElement.scrollTop -
            window.innerHeight <=
          10
        if (!bottomOfWindow) isLoading = false
        if (bottomOfWindow && !isLoading) {
          isLoading = true
          this.queryForm.page += 1
          if (this.data.length < this.count) this.query()
        }
      }
    },
  },
  mounted() {
    this.scroll()
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  font-weight: bolder;
  text-align: center;
  font-family: 'Helvetica Neue', Helvetica, 'PingFang SC', 'Hiragino Sans GB',
    'Microsoft YaHei', '微软雅黑', Arial, sans-serif;
}
.div_main {
  margin: 10px 10px 10px 10px;
}
.title {
  margin: 20px 0 20px 0;
}
.content {
  font-size: 16px;
}
</style>



